import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../views/index";

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index
  },
  {
    path: "/trading",
    name: "Trading",
    component: () => import(/* webpackChunkName: "Trading" */ "../views/trading")
  },
  {
    path: "/liquidity",
    name: "Liquidity",
    component: () => import(/* webpackChunkName: "Liquidity" */ "../views/liquidity")
  },
  {
    path: "/liquidity/pool/:pid",
    name: "LiquidityPool",
    component: () => import(/* webpackChunkName: "LiquidityPool" */ "../views/liquidity/pool")
  },
  {
    path: "/ieo",
    name: "Ieo",
    component: () => import(/* webpackChunkName: "Ieo" */ "../views/ieo")
  },
  {
    path: "/ieo/exchange/:id",
    name: "IeoExchange",
    component: () => import(/* webpackChunkName: "IeoExchange" */ "../views/ieo/exchange")
  },
  {
    path: "/ieo/add",
    name: "IeoAdd",
    component: () => import(/* webpackChunkName: "IeoAdd" */ "../views/ieo/add")
  },
  {
    path: "/bod",
    name: "Bod",
    component: () => import(/* webpackChunkName: "Bod" */ "../views/bod")
  },
  {
    path: "/page/:type",
    name: "IframePage",
    component: () => import(/* webpackChunkName: "IframePage" */ "../views/iframePage")
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
