<template>
  <div class="header-box">
    <el-row>
      <el-col :span="17"
        ><div class="grid-content flex-middle">
          <div class="img-box flex-middle">
            <img class="" :src="require('@/assets/logo.png')" />
            KISwap
          </div>
          
          <el-menu
            router
            :default-active="$route.path"
            @select="handleSelect"
            class="el-menu-demo"
            :class="{'mobileMenu-active': mobileShowMenu}"
            mode="horizontal"
            background-color="transparent"
            text-color="#DFE0E2"
            active-text-color="#02EAD0"
          >
            <el-menu-item index="/">{{$t('index.')}}</el-menu-item>
            <el-menu-item>
            <a class="href-a" :href="`https://spadesj.github.io/kis/app2/#/swap?theme=${themeMode}`">{{$t('index.')}}</a></el-menu-item>
            <el-menu-item>
            <a class="href-a" :href="`https://spadesj.github.io/kis/app2/#/pool?theme=${themeMode}`">{{$t('index.')}}</a></el-menu-item>
            <el-menu-item index="/trading">{{$t('index.')}}</el-menu-item>
            <el-menu-item index="/liquidity">{{$t('index.')}}</el-menu-item>
            <el-menu-item index="/bod">DAO</el-menu-item>
            <el-menu-item index="/ieo" v-if="$route.name === 'Index'">IKO</el-menu-item>
            <!-- <el-menu-item index="/bod">DAO</el-menu-item> -->
          </el-menu>
        </div></el-col>
        <el-col :span="7">
          <div class="grid-content flex-middle language">
            <el-dropdown @command="changeLanuage" class="language-el-dropdown">
              <span class="el-dropdown-link">
                <span class="pc-language">{{local === "zh-cn" ? $t('index.') : $t('index.')}}</span>
                <span class="mobile-language">{{local === "zh-cn" ? '' : 'EN'}}</span>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item :class="{'active': local === 'zh-cn'}" command="zh-cn">{{$t('index.')}}</el-dropdown-item>
                  <el-dropdown-item :class="{'active': local === 'en'}" command="en">{{$t('index.')}}</el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
            <div class="theme-btn flex-center" @click="changeThemeMode">
              <svg v-if="themeMode === 'dark'" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path></svg>
              <svg v-else xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><circle cx="12" cy="12" r="5"></circle><line x1="12" y1="1" x2="12" y2="3"></line><line x1="12" y1="21" x2="12" y2="23"></line><line x1="4.22" y1="4.22" x2="5.64" y2="5.64"></line><line x1="18.36" y1="18.36" x2="19.78" y2="19.78"></line><line x1="1" y1="12" x2="3" y2="12"></line><line x1="21" y1="12" x2="23" y2="12"></line><line x1="4.22" y1="19.78" x2="5.64" y2="18.36"></line><line x1="18.36" y1="5.64" x2="19.78" y2="4.22"></line></svg>
            </div>
            <div class="mobile-menu">
              <span @click="mobileShowMenu = !mobileShowMenu" class="nav-icon" :class="{'active': mobileShowMenu}"></span>
            </div>
          </div>
          <!-- <div class="flex-middle handle-btns" v-else>
            <div class="btn">{{$t('index.')}}</div>
            <div class="setting flex-center"><img src="~@/assets/image/h_setting.png" alt=""></div>
            <div class="more flex-center"><img src="~@/assets/image/h_more.png" alt=""></div>
          </div> -->
        </el-col>
    </el-row>
  </div>
</template>

<script>
import { reactive, toRefs, computed } from "vue";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";
import web3 from "web3"
import { getLang, setLang } from "@/utils/i18n"
import {useStore} from "vuex";
import { themeMode } from "@/storage"
export default {
  components: {},
  setup() {
    const router = useRouter();
    const store = useStore();
    const data = reactive({
      themeMode: computed(() => store.state.themeMode),
      mobileShowMenu: false,
      local: getLang().toLowerCase(),
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      handleSelect: () => {
        data.mobileShowMenu = false
      },
      changeThemeMode: () => {
        let theme = data.themeMode === 'dark' ? 'light' : 'dark'
        themeMode.set(theme)
        store.commit('updateThemeMode', theme)
      }
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss">
.language{
  justify-content: flex-end;
  padding-right: 48px;
  .el-dropdown{
    color: #DFE0E2;
    font-size: 18px;
  }
  .mobile-language {
    display: none;
  }
  .mobile-menu {
    display: none;
    height: 100%;
    .nav-icon {
      margin: 36px 0 0 10px;
      display: inline-block;
      position: relative;
      width: 20px;
      height: 2px;
      background-color: #DFE0E2;
      transition-property: background-color, transform;
      transition-duration: 300ms;
      &:before,
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: inherit;
        position: absolute;
        background:  #DFE0E2;
        transition-property: margin, transform;
        transition-duration: 300ms;
      }
      &:before {
        margin-top: -8px;
      }
      &:after {
        margin-top: 8px;
      }
      &.active {
        background: rgba(0, 0, 0, 0);
        &:before {
          margin-top: 0;
          transform: rotate(45deg);
        }
        &:after {
          margin-top: 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
.el-dropdown-menu__item.active{
  color: #188277;
}
.el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover {
    background-color: #e5fffc !important;
    color: #188277 !important;
}
.theme-light {
  .language{
    .el-dropdown{
      color: #333;
    }
  }
  .mobile-menu {
    .nav-icon {
      background-color: #333;
      &:before,
      &:after {
        background:  #333;
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .language {
    padding-right: 12px;
    .el-dropdown{
      font-size: 18px;
    }
    .pc-language {
      display: none;
    }
    .mobile-language {
      display: block;
    }
    .mobile-menu {
      display: block;
    }
  }
}
</style>

<style lang="scss" scoped>
.header-box {
  background: #02161A;
  .grid-content {
    height: 75px;
    .el-form--inline .el-form-item {
      margin-bottom: 0;
    }
    .el-menu.el-menu--horizontal {
      border: 0;
    }
    .el-menu--horizontal>.el-menu-item {
      border: 0 !important;
    }
    .el-menu-item.is-disabled {
      opacity: 1;
      color: #fff !important;
    }
    .el-menu--horizontal>.el-menu-item a.href-a {
      color: rgb(223, 224, 226) !important;
    }
    .img-box {
      height: 43px;
      line-height: 43px;
      white-space: nowrap;
      padding: 0 35px 0 50px;
      font-size: 22px;
      letter-spacing: 1px;
      color: #fff;
      font-weight: bold;
      img {
        height: 43px;
        margin-right: 18px;
      }
    }
  }
  .el-submenu__title:hover{
    background-color: transparent !important;
  }
  .el-menu-item:hover{
    background-color: transparent !important;
  }
  .el-menu-item {
    padding: 0 24px;
    font-size: 20px;
  }
  
  .handle-btns {
    height: 100%;
    justify-content: flex-end;
    color: #fff;
    padding-right: 48px;
    .btn,
    .setting,
    .more {
      height: 48px;
      margin-left: 20px;
      line-height: 48px;
      font-size: 20px;
    }
    .btn {
      background: #188277;
      padding: 0 10px;
      white-space: nowrap;
    }
    .setting,
    .more {
      width: 60px;
      border: 1px solid #08E8CE;
      text-align: center;
      img {
        width: 32px;
      }
    }
  }
  .theme-btn {
    color: #fff;
    margin: 0 0 0 15px;
    cursor: pointer;
    user-select: none;
    width: 36px;
    height: 35px;
    border: none;
    margin: 0;
    padding: 0;
    background-color: #40444F;
    margin-left: 8px;
    border-radius: 8px;
    svg {
      stroke: #08e8ce;
    }
    &:hover {
      background-color: #565A69;
    }
  }
}
.theme-light {
  .header-box {
    background: #c9e1c9;
    .grid-content {
      .el-menu--horizontal>.el-menu-item,
      .el-menu--horizontal>.el-menu-item a {
        color: #333333 !important;
      }
      .el-menu--horizontal>.el-menu-item.is-active,
      .el-menu--horizontal>.el-menu-item.is-active a {
        color: #3A7343 !important;
      }
      .el-menu--horizontal>.el-menu-item.is-disabled,
      .el-menu--horizontal>.el-menu-item.is-disabled a {
        color: #333333 !important;
      }
      .el-menu--horizontal>.el-menu-item a.href-a {
        color: #333333 !important;
      }
      .img-box {
        color: #333333;
      }
    }
    .theme-btn {
      background: #EDEEF2;
      svg {
        stroke: #3A7343;
      }
      &:hover {
        background-color: #CED0D9;
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .header-box {
    .grid-content {
      .img-box {
        padding: 0 12px;
      }
      .el-menu-item {
        font-size: 16px;
      }
      .el-menu-demo {
        display: none;
        position: fixed;
        top: 75px;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0,0,0,.8) !important;
        z-index: 500;
        .el-menu-item {
          float: none;
        }
        &.mobileMenu-active {
          display: block;
        }
      }
    }
  }
  .theme-light {
    .header-box {
      .grid-content {
        .el-menu--horizontal>.el-menu-item,
        .el-menu--horizontal>.el-menu-item a {
          color: #DFE0E2 !important;
        }
        .el-menu--horizontal>.el-menu-item.is-active,
        .el-menu--horizontal>.el-menu-item.is-active a {
          color: #02EAD0 !important;
        }
        .el-menu--horizontal>.el-menu-item.is-disabled,
        .el-menu--horizontal>.el-menu-item.is-disabled a {
          color: #DFE0E2 !important;
        }
        .el-menu--horizontal > .el-menu-item a.href-a {
          color: #DFE0E2 !important;
          font-weight: normal;
        }
      }
    }
  }
}
</style>
