import Web3 from "web3";
import store from "../store/index"
import factoryAbi from "@/utils/FactoryAbi";
import config from "@/utils/config";
const web3Data = {
  web3: {},
  factoryContract: {},
  web3Provider: {},
  getBlance: (account) => {
    web3Data.web3.eth.getBalance(account).then((amount) => {
      store.commit(
        "updateCurrentBalance",
        web3Data.web3.utils.fromWei(amount, "ether")
      );
    });
  },
  getKisUsdt: async () => {
    web3Data.factoryContract = new web3Data.web3.eth.Contract(factoryAbi, config.factoryAddress)
    let address = await web3Data.factoryContract.methods.getPair('0xeC5028AfBf20f09C4AD848bae1CD6f4B946Af8EC', '0xe579156f9dEcc4134B5E3A30a24Ac46BB8B01281').call()
    console.log(address)
    web3Data.factoryContract.methods.getReserves(config.kisAddress, config.usdtAddress).call().then((result) => {
      let tokenA = result.reserveA / Math.pow(10, 18)
      let tokenB = result.reserveB / Math.pow(10, 10)
      console.log(tokenA, tokenB)
      let rate = tokenA / tokenB
      console.log('rate ----',rate)
      store.commit('updateRate', rate)
     })
  },
  initWeb3: async () => {
    if (window.ethereum) {
      web3Data.web3Provider = window.ethereum;
      window.ethereum.on("accountsChanged", (accounts) => {
        store.commit("updateAccount", accounts);
      })
      web3Data.web3 = new Web3(web3Data.web3Provider);
      try {
        await window.ethereum.enable();
      } catch (error) {
        Promise.reject(error)
      }
    } else {
      if (window.web3) {
        web3Data.web3Provider = window.web3.currentProvider;
        web3Data.web3 = new Web3(web3Data.web3Provider);
      } else {
        web3Data.web3Provider = new Web3.providers.HttpProvider(
          "https://exchaintest.okexcn.com"
        );
        web3Data.web3 = new Web3(web3Data.web3Provider);
      }
    }
    store.commit("updateIsConnected", true);
    store.commit("updateProvider", web3Data.web3);
    web3Data.web3.eth.getAccounts().then((accounts) => {
      store.commit("updateAccount", accounts);
      web3Data.getKisUsdt()
      return web3Data.getBlance(accounts[0]);
    });
  }
}

export default web3Data;
