<template>
  <el-container>
    <el-header>
      <Header />
    </el-header>
    <el-main>
      <router-view />
      <Footer />
    </el-main>
  </el-container>
</template>
<script>
import { reactive, toRefs, computed, watch } from "vue";
import {useStore} from "vuex";
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import web3Data from "@/utils/initWeb3";
export default {
  components: {
    Header,
    Footer
  },
  setup () {
    const store = useStore();
    const data = reactive({
      themeMode: computed(() => store.state.themeMode)
    });
    document.getElementsByTagName('body')[0].className= `theme-${data.themeMode}`;
    watch(() => data.themeMode, (val) => {
      if (val) {
        document.getElementsByTagName('body')[0].className= `theme-${data.themeMode}`;
      }
    })
    const refData = toRefs(data);
      web3Data.initWeb3()
    return {
      ...refData
    };
  //  let web3 = new Web3(web3Provider);
  //  let factoryContract = new web3.eth.Contract(factoryAbi, '0xc807D8D923945eF20731c9b4861847e89A32DA02')
  //  factoryContract.methods.getReserves('0x045F8EdA5aDD0bd03442d85a563764518CcFB2bd', '0xe579156f9dEcc4134B5E3A30a24Ac46BB8B01281').call().then((result) => {
  //    console.log(result)
  //  })
  }
};
</script>
