<script>
import IndexTop from "@/views/index/IndexTop";
import { CountTo } from 'vue3-count-to';
import { reactive, toRefs, computed, watch} from "vue";
import poolAbi from "@/utils/poolAbi";
import {useStore} from "vuex";
import factoryAbi from "@/utils/FactoryAbi";
import poolList from "@/utils/poolList";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
import { getLang } from "@/utils/i18n"
export default {
  components: {
    IndexTop,
    CountTo
  },
  setup () {
     const store = useStore();
     const data = reactive({
       local: computed(() => store.state.locale),
       themeMode: computed(() => store.state.themeMode),
       poolPairList: poolList,
       rate: computed(() => store.state.rate),
       web3: computed(() => store.state.provider),
       poolTotalLock: 0,
       totalAllocPoint: 0,
       totalMintDay: 0,
       totalMint: 0,
       kisBlance: 0,
       address: computed(() => store.state.accounts[0]),
       poolAddress: config.poolAddress,
       isLp: true,
       hgAmount: 0,
       toggleLP: () => {
         data.isLp = !data.isLp
       },
        getRecords:async () => {
         let tokenAContract = new data.web3.eth.Contract(tokenAbi, config.kisAddress);
         let blance = await tokenAContract.methods.balanceOf(config.blackHole).call();
         data.hgAmount = Number(blance/Math.pow(10, 18)).toFixed(2)
       },
       getPoolReward: () => {
         let poolContract = new data.web3.eth.Contract(poolAbi, data.poolAddress)
         poolContract.methods.totalAllocPoint().call().then((result) => {
           data.totalAllocPoint = result
           console.log('totalAllocPoint',result)
         })
         data.web3.eth.getBlockNumber().then((block) => {
            poolContract.methods.getMdxBlockReward(block).call().then(async (result) => {
              console.log('11111111',result)
              let startBlock = await poolContract.methods.startBlock().call()
              data.totalMint = (block - startBlock) * result / Math.pow(10, 18)
              data.totalMintDay = result * 28800 / Math.pow(10, 18)
            })
         })
       },
       getRate: () => {
         let total = 0;
         data.poolPairList.forEach(async (item) => {
           console.log(item)
           if (item.islp) {
              let factoryContract = new data.web3.eth.Contract(factoryAbi, config.factoryAddress)
              let price = await factoryContract.methods.getReserves(item.tokenA, config.usdtAddress).call()
              let tokenAContract = new data.web3.eth.Contract(tokenAbi, item.tokenA)
              let lpContract = new data.web3.eth.Contract(tokenAbi, item.lpAddresses)
              factoryContract.methods.getReserves(item.tokenA, item.tokenB).call().then( async (result) => {
                let decimalsA = await tokenAContract.methods.decimals().call()
                // let decimalsB = await tokenBContract.methods.decimals().call()
                let perUsdtA = price.reserveB * Math.pow(10, decimalsA - 10) / price.reserveA
                let tokenUsdt = result.reserveA / Math.pow(10, decimalsA) / perUsdtA
                item.usdt = tokenUsdt * 2
                let blance = await lpContract.methods.balanceOf(data.poolAddress).call()
                let totalply = await lpContract.methods.totalSupply().call()
                console.log(item.usdt)
                item.usdt = item.usdt * (blance/totalply);
                data.poolTotalLock = data.poolTotalLock + item.usdt
              })
           } else {
              let lpContract = new data.web3.eth.Contract(tokenAbi, item.lpAddresses)
              let blance = await lpContract.methods.balanceOf(data.poolAddress).call()
               let lPdecimals = await lpContract.methods.decimals().call()
  
             let factoryContract = new data.web3.eth.Contract(factoryAbi, config.factoryAddress)
             let price;
             try {
               price = await factoryContract.methods.getReserves(item.lpAddresses, config.usdtAddress).call()
              let perUsdtA = price.reserveB * Math.pow(10, lPdecimals - 10) / price.reserveA
              let tokenUsdt = blance / Math.pow(10, lPdecimals) / perUsdtA
              item.usdt = tokenUsdt
             } catch (error) {
               console.log('blance----', blance)
               item.usdt = blance / Math.pow(10, lPdecimals);
             }
             data.poolTotalLock = data.poolTotalLock + item.usdt
           }
         });
       },
       getBlanceKis: async () => {
          let tokenAContract = new data.web3.eth.Contract(tokenAbi, config.kisAddress);
          let decimals = await tokenAContract.methods.decimals().call()
          let blance = await tokenAContract.methods.balanceOf(data.address).call()
          data.kisBlance = Number(blance / Math.pow(10, decimals)).toFixed(2)
       }
     })
     if (data.address) {
        data.getBlanceKis()
        data.getPoolReward()
        data.getRate()
        data.getRecords()
     }
     watch(() => data.address, (val) => {
       if (val) {
         data.getBlanceKis()
         data.getPoolReward()
         data.getRate()
          data.getRecords()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
